
.position {
height: 18vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.loader {

    border: 6px solid #f3f3f3;
    border-radius: 50%;
    border-top: 6px solid #380076;
    width: 50px;
    height: 50px;
    -webkit-animation: spin 2s linear infinite; /* Safari */
    animation: spin 2s linear infinite;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
